import React from 'react'
import PropTypes from "prop-types"

import { CustomHeader } from "./styles"

const Header = ({ siteTitle }) => (
  <CustomHeader>
    {siteTitle}
  </CustomHeader>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header